.Portifolio__container {
  padding-bottom: 50px;
  max-width: 850px;
  margin: 0 auto;
}

@media (max-width: 940px) {
  .Portifolio__container {
    padding: 30px;
  }
}

.portifolio__categories li {
  margin: 0 20px;
  padding: 15px 0;
  cursor: pointer;
}

.portifolio__categories .active {
  border-bottom: 2px solid #0bceaf;
}

/* *********************mosaic effect */

#photos {
  /* Prevent vertical gaps */
  line-height: 0;
  -webkit-column-count: 5;
  -webkit-column-gap: 30px;
  -moz-column-count: 5;
  -moz-column-gap: 30px;
  column-count: 3;
  column-gap: 30px;
  margin: 0 auto;
  transition: 0.5s ease;
  width: 100%;
}

#photos img {
  /* Just in case there are inline attributes */
  width: 100% !important;
  height: auto !important;
  /* padding: 10px; */
  margin-bottom: 30px;
}

#photos > img {
  margin: 0px;
}

@media (max-width: 1200px) {
  #photos {
    -moz-column-count: 4;
    -webkit-column-count: 4;
    column-count: 4;
  }
}
@media (max-width: 1000px) {
  #photos {
    -moz-column-count: 3;
    -webkit-column-count: 3;
    column-count: 3;
  }
}
@media (max-width: 800px) {
  #photos {
    -moz-column-count: 3;
    -webkit-column-count: 3;
    column-count: 3;
  }
}
@media (max-width: 416px) {
  #photos {
    -moz-column-count: 2;
    -webkit-column-count: 2;
    column-count: 2;
  }
}

/* *********************end mosaic effect */

.container-img {
  position: relative;
}

.portifolio_img {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: 0.5s ease;
  backface-visibility: hidden;
  border-radius: 1rem;

  /* animation: show_portifolio_img 0.6s ease-out forwards; */
}

@keyframes show_portifolio_img {
  from {
    transform: scale(0.3);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.middle {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  width: 80%;
}

.container-img:hover .portifolio_img {
  cursor: pointer;
  opacity: 0.3;
}

.container-img:hover .middle {
  opacity: 1;
  cursor: pointer;
}

.text {
  background-color: #0bceaf;
  color: white;
  font-size: 16px;
  padding: 16px 32px;
}

.my-node-enter {
  opacity: 0;
}
.my-node-enter-active {
  opacity: 1;
  animation: grow 0.5s ease-out forwards;
}
/* .my-node-exit {
  opacity: 1;
  animation: shrink 2.5s ease-out forwards;
} */
/* .my-node-exit-active {
  opacity: 1;
  animation: shrink 2.5s ease-out forwards;
} */

@keyframes grow {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes shrink {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0);
  }
}
