/* *********************mosaic effect */

#photos {
  /* Prevent vertical gaps */
  line-height: 0;
  -webkit-column-count: 5;
  -webkit-column-gap: 30px;
  -moz-column-count: 5;
  -moz-column-gap: 30px;
  -webkit-column-count: 3;
  column-count: 3;
  grid-column-gap: 30px;
  column-gap: 30px;
  margin: 0 auto;
  transition: 0.5s ease;
  width: 100%;
}

#photos img {
  /* Just in case there are inline attributes */
  width: 100% !important;
  height: auto !important;
  /* padding: 10px; */
  margin-bottom: 30px;
}

#photos > img {
  margin: 0px;
}

@media (max-width: 1200px) {
  #photos {
    -webkit-column-count: 4;
    column-count: 4;
  }
}

@media (max-width: 1000px) {
  #photos {
    -webkit-column-count: 3;
    column-count: 3;
  }
}

@media (max-width: 800px) {
  #photos {
    -webkit-column-count: 3;
    column-count: 3;
  }
}

@media (max-width: 416px) {
  #photos {
    -webkit-column-count: 2;
    column-count: 2;
  }
}

/* *********************end mosaic effect */

.container-img {
  position: relative;
}

.portifolio_img {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: 0.5s ease;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 1rem;
  /* animation: show_portifolio_img 0.6s ease-out forwards; */
}

@keyframes show_portifolio_img {
  from {
    transform: scale(0.3);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}

.middle {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  width: 80%;
}

.container-img:hover .portifolio_img {
  cursor: pointer;
  opacity: 0.3;
}

.container-img:hover .middle {
  opacity: 1;
  cursor: pointer;
}

.text {
  background-color: #0bceaf;
  color: white;
  font-size: 16px;
  padding: 16px 32px;
}

.my-node-enter {
  opacity: 0;
}

.my-node-enter-active {
  opacity: 1;
  animation: grow 0.5s ease-out forwards;
}

/* .my-node-exit {
  opacity: 1;
  animation: shrink 2.5s ease-out forwards;
} */

/* .my-node-exit-active {
  opacity: 1;
  animation: shrink 2.5s ease-out forwards;
} */

@keyframes grow {
  from {
    opacity: 0;
    transform: scale(0);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes shrink {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(0);
  }
}

.Modal__modal {
  position: fixed; /* Stay in place */
  z-index: 1000; /* Sit on top */
  padding-top: 40px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.7); /* Black w/ opacity */
}

/* Modal Content */

.Modal__modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 30px;
  border: 1px solid #888;
  width: 95%;
  max-height: 90vh;
  overflow-y: scroll;
  padding-right: 40px;
  position: relative;
  animation: openModal 0.1s ease-out forwards;
}

@keyframes openModal {
  0% {
    opacity: 0;
    /* transform: translateY(100%); */
  }

  50% {
    opacity: 0.5;
    /* transform: translateY(90%); */
  }

  100% {
    opacity: 1;
    /* transform: translateY(0%); */
  }
}

/* The Close Button */

.Modal__close_button {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  right: 60px;
  z-index: 10;
}

.Modal__close_button:hover,
.Modal__close_button:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

/* *********************** styling scrool bar */

::-webkit-scrollbar {
  width: 16px;
  height: 18px;
}

::-webkit-scrollbar-thumb {
  height: 6px;
  border: 4px solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
  -webkit-border-radius: 7px;
  background-color: rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: inset -1px -1px 0px rgba(0, 0, 0, 0.05),
    inset 1px 1px 0px rgba(0, 0, 0, 0.05);
}

::-webkit-scrollbar-button {
  width: 0;
  height: 0;
  display: none;
}

::-webkit-scrollbar-corner {
  background-color: transparent;
}

